import {
	request
} from '../../request.js'
export default {
	data() {
		return {
			baseURL:this.$api,
			visible:0,
			navList: ['全部', '待付款', '待发货', '待收货', '待评价'],
			currentIndex: 0,
			list: [],

		};

	},
	onLoad: function(params) {
		if(params.status == 'all') {
			this.currentIndex = 0
		}
		else {
			this.currentIndex = params.status;
		}
		
		this.getList();
	},
	methods: {
		// 点击切换
		handleNav(index) {
			this.currentIndex = index;
			this.getList();
		},
		// 点击商品去到订单详情页
		orderDetail(id) {
			uni.navigateTo({
				url: '/pages/components/order-detail/order-detail?id='+id
			})
		},
		// 店家跳转到申请退货页面
		returnGoods() {
			uni.navigateTo({
				url: '/pages/components/return-goods/return-goods'
			})
		},
		// 点击跳转到评价页面
		appraise() {
			uni.navigateTo({
				url: '/pages/components/appraise/appraise'
			})
		},
		getList() {
			this.visible = 0;
			let status = '';
			let that = this;
			let index = parseInt(this.currentIndex);
			switch (index) {
				case 0:
					status = 'all';
					break;
				case 1:
					status = '0';
					break;
				case 2:
					status = '1';
					break;
				case 3:
					status = '2';
					break;
				case 4:
					status = '3';
					break;
				default:
					status = '6';
			}

			request({
				url: 'index.php?s=/wap/order/apiMyOrderList',
				data: {
					status: status
				},
				method: "POST",
			}).then(function(res) {
				that.visible = 1;
				that.list = res.data.order_list.data;
			});
		},
		//订单确认收货
		OrderTakeDelivery(order_id){
			let that = this;
			uni.showModal({
				content: '确认收到货品了吗？',
				success: res => {
					if(res.confirm) {
						request({
							url:'index.php?s=/wap/order/OrderTakeDelivery',
							method:"POST",
							data:{order_id:order_id},
						}).then(res=>{
							if(res.data>0){
								uni.showToast({
									title:"确认收货成功!",
									icon:"none",
								});
								that.getList();
							}else{
								uni.showToast({
									title:"操作失败!",
									icon:"none",
								});
							}
						});
					}
				}
			})
		},
		//查看物流
		orderLogistics(order_id){
			uni.navigateTo({
				url:"/pages/components/logistics/logistics?id="+order_id,
			})
		},
		//取消订单
		orderClose(order_id){
			let that = this;
			uni.showModal({
				content: '确定要取消该订单吗？',
				success: res => {
					if(res.confirm) {
						request({
							url:'index.php?s=/wap/order/apiOrderClose',
							method:"POST",
							data:{order_id:order_id},
						}).then(res=>{
							that.getList();
						});
					}
				}
			})
		},
		//删除订单
		orderDeleteOrder(order_id){
			let that = this;
			uni.showModal({
				content: '确定要删除该订单吗？',
				success: res => {
					if(res.confirm) {
						request({
							url:'index.php?s=/wap/order/apiDeleteOrder',
							method:"POST",
							data:{order_id:order_id},
						}).then(res=>{
							that.getList();
						});
					}
				}
			})
		},
		//支付订单
		payOrder(order_no){
			if(order_no ==""){
				uni.showToast({
					title:"订单错误,请重新进入本页面!",
					icon:"none"
				});
				return ;
			}
			let token = uni.getStorageSync("token");
			console.log(token[0]);
			if(token[0] == ""||token[0]==null||token[0]==0){
				uni.showToast({
					title:"登录过期!请重新登录",
					icon:"none"
				});
				setTimeout(function(){
					uni.navigateTo({
						url:"/pages/components/login/login"
					});
				},1000);
			}
			let redirectUrl = encodeURIComponent("https://admin.dxsc.vip/index.php?s=/wap/pay/ApiwchatPay&no="+order_no+"&token="+token[0]);
			let url = 'https://open.weixin.qq.com/connect/oauth2/authorize?appid=wx7cffa961be69b86e&redirect_uri='+redirectUrl+'&response_type=code&scope=snsapi_userinfo&state=STATE#wechat_redirect';
			window.location.href = url;
		},
		orderEvaluate(order_id){
			uni.navigateTo({
				url:"/pages/components/appraise/appraise?id="+order_id
			});
		},
		refundGoods(id){
			uni.navigateTo({
				url:"/pages/components/refund/refund?id="+id
			});
		},
	}
}
